export default {
  name: 'activityRuleInfo',
  data () {
    return {
      details: ''
    }
  },
  methods: {
    getData () {
      this.$http(this.$urlPath.getActivityRuleDetails, {
        assist_id: this.$routeParams.aid
      }, '', (res) => {
        this.details = res.data
      }, (errorCode, error) => {
        this.$toast(error)
      })
    }
  },
  mounted () {
    this.getData()
  }
}
